import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

function showToDos(arr) {
  let listContainer = document.querySelector(".todoList");
  let toDoList = ``;
  let classText;

  for (let i = 0; i < arr.length; i++) {
    if (arr[i].todoComplete == true) {
      classText = `done`;
    } else {
      classText = ``;
    }

    let toDoTemplate = `<li class="${classText} toDoItem">
    ${arr[i].todoText}<span> <i class="fa fa-trash"></i></span>
    </li>`;
    toDoList += toDoTemplate;
  }
  listContainer.innerHTML = toDoList;
}

document.querySelector("#inputBtn").addEventListener("click", () => {
  let input = document.querySelector("#userInput");
  let inputValue = input.value;

  let newToDo = {
    todoID: todos.length,
    todoText: inputValue,
    todoComplete: false
  };
  todos.push(newToDo);
  input.value = ``;
  showToDos(todos);
});

document.addEventListener("keypress", (e) => {
  if (e.key === "Enter") {
    let input = document.querySelector("#userInput");
    let inputValue = input.value;

    let newToDo = {
      todoID: todos.length,
      todoText: inputValue,
      todoComplete: false
    };
    todos.push(newToDo);
    input.value = ``;
    showToDos(todos);
  }
});

//document.querySelectorAll(".toDoItem").addEventListener("click", () => {});

showToDos(todos);
